    jump(item) {
      if (!item.children) {
        this.socket.emit("padevent", item.id);
        this.$router.push({ query: { id: item.id } });
      }
    },
    jumpPPT(flag) {
      this.socket.emit("padevent", flag);
    }
  },
  created() {
    this.socket = io.connect(this.ipaddress);
    this.socket.on("paddata", data => {
      this.$router.push({ query: { id: data } });
      console.log(data);
    });
