<style scoped>
.mainnav {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 5%;
  color: #fff;
  text-align: center;
  font-size: 16px;
}
.navtitle {
  font-size: 26px;
  font-weight: bold;
  border-bottom: 1px solid white;
  padding: 15px 0;
}
.subtitle {
  font-size: 20px;
  border-bottom: 1px solid white;
  border-top: 1px solid white;
  padding: 10px 0;
}
.thirdTitle {
  padding: 5px 0;
}
.navWrapper {
  margin: 0 auto;
  width: 78%;
}
.navWrapper::after {
  content: "";
  clear: both;
  visibility: hidden;
  display: block;
}
.navWrapper .subnav {
  float: left;
  padding: 10px;
  background-color: #999;
  margin: 0 10px;
}
ul {
  list-style: none;
}
.ppt {
  position: absolute;
  text-align: center;
  line-height: 80px;
  font-size: 36px;
  color: #fff;
  top: 50%;
  transform: translate(0, -50%);
  width: 100%;
  z-index: 100;
}
.prep {
  background-color: #999;
  width: 80px;
  height: 80px;
  position: absolute;
  left: 5%;
  border-radius: 50%;
}
.next {
  background-color: #999;
  width: 80px;
  height: 80px;
  position: absolute;
  right: 5%;
  border-radius: 50%;
}
.btninner {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
</style>



