<template>
  <div class="padControl">
    <main-nav v-if="flag" :ipaddress='ipaddress'></main-nav>
  </div>
</template>
<style>

</style>
<script>
import mainNav from './mainnav'
export default {
  name:"padControl",
  components:{
    mainNav
  },
  data() {
    return {
      flag: false,
      ipaddress: '' 
    }
  },
  created() {
    this.$http.get('./static/ipaddress.json').then(response => {
      this.ipaddress = response.data.ipaddress;
      this.flag = true;
    })
  }
}
</script>



